<template>
    <div class="mui-content" style="padding-top: 44px">

        <div class="mui-card" v-for="item in datas">
            <!--页眉，放置标题-->
            <div class="mui-card-header">
                <div class="mui-media-body">
                    发送给: <span>{{item.uid2}}</span>
                    <p>{{item.ltime}}</p>
                </div>

            </div>
            <!--内容区-->
            <div class="mui-card-content">
                <h4>{{item.title}}</h4>
                <p>
                    {{item.con}}
                </p>

            </div>

            <div class="mui-card-footer">
                发送人: {{item.uid1}}
            </div>
        </div>


    </div>
</template>

<script>
    export default {
        name: 'hello',
        data () {
            return {
                type:"",
                datas:[]
            }
        },
        created(){
            var params="type="+this.$route.params.type+"&uid="+this.$store.state.uid;
            fetch("/api/selectLogs?"+params).then(function (e) {
                return e.json();
            }).then((e)=> {
                    this.datas=e;
            })

        },
        watch:{
            $route(){
                alert(1);
                var params="type="+this.$route.params.type+"&uid="+this.$store.state.uid;
                fetch("/api/selectLogs?"+params).then(function (e) {
                    return e.json();
                }).then((e)=> {
                    this.datas=e;
                })
            }
        }
    }
</script>

<style scoped>

</style>


