<template>
    <div style="position: absolute;left:0;top:0;width: 100%;height:100%">


        <header class="mui-bar mui-bar-nav">
            <a class="mui-action-back mui-icon mui-icon-left-nav mui-pull-left" @click="back"></a>
            <h1 class="mui-title">联系方式</h1>
        </header>
        <ul class="mui-table-view" style="padding: 44px 0">
            <li class="mui-table-view-cell" v-for="item in datas">

                <a :href="'tel:'+item.phone" :ref="item.pin">
                    {{item.uname}}
                </a>
            </li>
        </ul>

        <ul class="aside">
            <li v-for="item in datas" @click="scroll(item.pin)">
                {{item.pin}}
            </li>
        </ul>

        <Nav></Nav>
    </div>
</template>

<script>
    import Nav from "@/components/nav.vue"
    export default {
        name: 'hello',
        components:{Nav},
        data () {
            return {
                datas:[],
                pins:[]
            }
        },
        created(){
            fetch("/api/selectPhone").then(function (e) {
                return e.json()
            }).then((e)=>{

                for(var i=0;i<e.length;i++){
                    e[i].pin=pinyinUtil.getFirstLetter(e[i].uname).substr(0,1);


                }




                function sort(arr) {
                    for(var i=0;i<arr.length;i++){
                        for(var j=i+1;j<arr.length;j++){
                            if(arr[i].pin.charCodeAt(0)>arr[j].pin.charCodeAt(0)){
                            var temp=arr[i];
                                arr[i]=arr[j];
                                arr[j]=temp;
                            }

                        }
                    }
                }
                sort(e);
                this.datas=e;

            })
        },
        methods:{
            back(){
                this.$router.back();
            },
            scroll(pin){
                window.scrollTo(0,this.$refs[pin][0].getBoundingClientRect().top);
                //console.log(this.$refs[pin][0].getBoundingClientRect().top)
            }
        }
    }
</script>

<style scoped>
.aside{
    width:15px;height:100vh;
    padding:44px 0;
    box-sizing: border-box;
    position: fixed;
    right:0;top:0;
    background: rgba(200,200,200,.6);
}
.aside li{
    list-style: none;
    width:15px;line-height: 20px;
    font-size: 12px;
}
</style>


