<template>
    <div style="position: absolute;left:0;top:0;width: 100%;">
    <header class="mui-bar mui-bar-nav">
        <a class="mui-action-back mui-icon mui-icon-left-nav mui-pull-left" @click="back"></a>
        <h1 class="mui-title">详情</h1>
    </header>
        <div class="mui-content">
            <h3>
                {{title}}
            </h3>
            <p>
                {{con}}
            </p>

        </div>
    </div>
</template>

<script>
    export default {
        name: 'hello',
        data () {
            return {
                title:"",
                con:""
            }
        },
        methods:{
            back(){
                this.$router.back();
            }
        },
        created(){
            fetch("/api/conSelect?nid="+this.$route.params.nid).then(function (e) {
               return e.json();
            }).then((e)=> {
                this.title=e.title;
                this.con=e.con;
            })
        }
    }
</script>

<style scoped>

</style>


