<template>
    <div class="mui-content">
        <ul class="mui-table-view" v-if="datas.length>0">

            <li class="mui-table-view-cell" v-for="item in datas">
                <router-link class="mui-navigate-right" :to="'/partList/'+item.pid" v-if="item.son">
                    {{item.pname}}
                </router-link>
                <span v-else="!item.son" @click="selectUser(item.pid)">
                     {{item.pname}}
                </span>

            </li>
        </ul>

        <div v-if="users.length>0">
        <ul class="mui-table-view" >
            <li class="mui-table-view-cell" v-for="item in users" @click="select(item)" :style="item.style">
                {{item.uname}}
            </li>

        </ul>
            <router-link to="/write" class="btn blue">
                选择
            </router-link>
        </div>
        <div v-if="(users.length==0&&datas.length==0)">
            <li class="mui-table-view-cell">
                没有人
            </li>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'hello',
        data () {
            return {
                datas:[],
                users:[]
            }
        },
        created(){
            this.users=[];
            var pid=this.$route.params.pid||0;
            fetch("/api/clientUserPart?pid="+pid).then(function (e) {
                return e.json();
            }).then((e)=> {
                this.datas=e;
            })
        },
        methods:{
            selectUser(pid){
                this.datas=[];
                fetch("/api/clientSelectUser?pid="+pid).then(function (e) {
                    return e.json();

                }).then( (e)=> {
                     this.users=e
                })
            },
            select(obj){

                this.users.forEach(function (item) {
                    item.style={};
                })
                obj.style={color:"red"};

                this.$store.commit("setUserId",obj.uid);
                this.$store.commit("setUserName",obj.uname);


            }


        },
        watch:{

            $route(){
                this.users=[];
                var pid=this.$route.params.pid||0;
                fetch("/api/clientUserPart?pid="+pid).then(function (e) {
                    return e.json();
                }).then((e)=> {
                    this.datas=e;
                })
            }
        }
    }
</script>

<style scoped>
    .btn{
        display: block;
        width:100%;
        border:1px solid #ccc;
        margin:auto;
        color:#000;
        position: fixed;
        padding:10px 0;
        bottom: 0;
        left:0;
    }
    .blue{
        background: #6bb1ff;
        margin-top: 10px;
    }
</style>


