<template>

 <div style="position: absolute;left:0;top:0;width: 100%;height:100%;">
     <header class="mui-bar mui-bar-nav">

         <h1 class="mui-title">
             标题11
         </h1>
         <a class="mui-action-back mui-icon mui-icon-closeempty mui-pull-right" @click="logout">

         </a>
     </header>

     <div class="mui-content" style="position: absolute;top:0;width:100%">

         <slider>
             <div class="slider-list" style="background: red">
                 1
             </div>
             <div class="slider-list" style="background: blue">
                 2
             </div>
             <div class="slider-list" style="background: pink">
                 3
             </div>
         </slider>



         <pullDown :fn="fn" :autoload="true">
             <ul class="mui-table-view">
                 <li class="mui-table-view-cell" v-for="item in datas">
                     <router-link :to="'/con/'+item.nid" class="mui-navigate-right">
                         {{item.title}}
                     </router-link>
                 </li>
             </ul>
         </pullDown>


         <Nav></Nav>
     </div>

 </div>
</template>

<script>
    import slider from "@/components/slider.vue"
    import pullDown from "@/components/pullDown.vue"
    import Nav from "@/components/nav.vue"
    export default {
        name: 'hello',
        components:{slider,pullDown,Nav},
        data () {
            return {
                datas:[],
                page:this.$store.state.page
            }
        },

        destroyed(){
            this.$store.commit("setPullDown",false)
        },
        /*
        created(){
            fetch("/api/newSelect").then(function (e) {
                return e.json()

            }).then((e)=> {
                this.datas=e;
            })
        },
        */
        methods:{
            logout(){

                this.$store.commit("setLogin","");
                this.$store.commit("setUname","");
                this.$store.commit("setUid","");
                this.$router.push("/login");
            },
            fn(obj){
                if (this.$store.state.pullDown) {
                    fetch("/api/newSelect?page=" + this.page + "&num=10").then(function (e) {
                        return e.json()
                    }).then((e) => {
                        setTimeout(() => {
                            console.log(this.page);
                            this.$store.commit("setPage",++this.page);
                            this.datas = e.concat(this.$store.state.message);
                            this.$store.commit("setMessage", this.datas);
                            obj.over();

                        }, 2000)

                    })
                }else{
                    this.datas=this.$store.state.message;
                    this.$store.commit("setPullDown",true)
                }
            }
        }
    }
</script>

<style scoped>

</style>


