<template>
    <div class="pullDownBox" @touchstart="start($event)" ref="pullDownBox">
        <div class="pullDownMessage">
            {{message}}
        </div>
        <div class="pullDownCon" ref="pullDownCon">
            <slot></slot>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'hello',
        props:["fn","autoload"],
        data () {
            return {
                startx:0,
                starty:0,
                movex:0,
                movey:0,
                pullDownBox:null,
                pullDownCon:null,
                message:"下拉刷新",
                flag:true,
                length:0
            }
        },
        mounted(){
           this.pullDownBox=this.$refs["pullDownBox"];
            this.pullDownCon=this.$refs["pullDownCon"];
            if(this.autoload){
                if(this.fn){
                    this.fn(this);
                }
            }
        },
        methods:{
            start(e){

                if(!this.flag){
                    return;
                }
                this.length=0;
                this.message="下拉更新"
                this.pullDownCon.style.transition="none"
                this.startx=e.touches[0].pageX;
                this.starty=e.touches[0].pageY;
                this.pullDownBox.addEventListener("touchmove",this.move)
                document.body.addEventListener("touchend",this.end)

            },
            move(e){
                this.movex=e.touches[0].pageX;
                this.movey=e.touches[0].pageY;
                if(this.movey>this.starty&&Math.abs(this.movey-this.starty)>5){
                    var length=(this.movey-this.starty-5)/5;
                    this.length=length;


                    this.pullDownCon.style.transform="translate(0,"+length+"px)"
                }
            },
            end(e){

                if(this.length==0){
                    return;
                }

                this.flag=false;
                this.message="正在更新...."
                this.pullDownBox.removeEventListener("touchmove",this.move)
                document.body.removeEventListener("touchend",this.end);
                this.pullDownCon.style.transition="transform .5s ease"
                this.pullDownCon.style.transform="translate(0,44px)";
                if(this.fn) {
                    this.fn(this);
                }

            },
            over(){
                this.flag=true;
                this.message="更新完毕";
                this.pullDownCon.style.transform="translate(0,0px)";

            }
        }
    }
</script>

<style scoped>

    .pullDownBox{
        overflow-x: hidden;
        left:0;top:0;
        position: relative;

    }
    .pullDownMessage{
        position: absolute;
        width:100%;height:44px;
        left:0;top:0;
        text-align: center;
        line-height: 44px;
    }
    .pullDownCon{
        width:100%;
    }

</style>


