import Vue from "vue";
import Vuex from "vuex";
Vue.use(Vuex);
var store=new Vuex.Store({
    state: {
       message:[],
       pullDown:true,
       page:0,
        userId:"",
        userName:"",
        title:"",
        con:"",
        login:"",
        uname:"",
        uid:""
    },
    mutations: {
        setMessage(state,message){
            state.message=message;
        },
        setPullDown(state,val){
            state.pullDown=val;
        },
        setPage(state,val){
            state.page=val;
        },
        setUserId(state,val){
            state.userId=val;
        },
        setUserName(state,val){
            state.userName=val;
        },
        setTitle(state,val){
            state.title=val;
        },
        setCon(state,val){
            state.con=val;
        },
        setLogin(state,val){
            state.login=val;
        },
        setUname(state,val){
            state.uname=val;
        },
        setUid(state,val){
            state.uid=val;
        }
    }
})

export default  store;


