<template>
    <form class="mui-input-group">
        <div class="mui-input-row">
            <label>用户名</label>
            <input type="text" class="mui-input-clear" placeholder="请输入用户名" v-model="uname">
        </div>
        <div class="mui-input-row">
            <label>密码</label>
            <input type="password" class="mui-input-password" placeholder="请输入密码" v-model="upass">
        </div>
        <div class="mui-button-row">
            <button type="button" class="mui-btn mui-btn-primary" @click="submitForm">确认</button>
            <button type="button" class="mui-btn mui-btn-danger" >取消</button>
        </div>
    </form>

</template>
<script>

    export default {
        data() {

            return{
                uname:"",
                upass:""
            }

        },
        methods: {
            submitForm(formName) {

                        fetch("/api/checkClientLogin",{
                            method:"post",
                            headers:{"content-type":"application/x-www-form-urlencoded"
                            },
                            body:"uname="+this.uname+"&upass="+this.upass
                        }).then(function (e) {
                            return e.json();
                        }).then( (e)=> {
                            console.log(e);
                    if(e.message=="ok"){
                       this.$store.commit("setLogin","yes");
                        this.$store.commit("setUname",e.data[0].uname);
                        this.$store.commit("setUid",e.data[0].uid);
                        this.$router.push("/")
                    }else{
                        this.message="用户名或密码错误"
                    }
                        });


            }
        }
    }
</script>

<style scoped>

</style>


