import Vue from "vue";
import VueRouter from "vue-router";
import Index from "@/components/Index"
import Con from "@/components/Con"
import Phone from "@/components/Phone"
import Logs from "@/components/Logs"
import ShowLogs from "@/components/ShowLogs"
import SendMessage from "@/components/SendMessage"
import WriteLog from "@/components/WriteLog"
import selectPart from "@/components/selectPart"
import Login from "@/components/login"



Vue.use(VueRouter);

var router=new VueRouter({
    linkActiveClass:"mui-active",
    routes:[
        {
            path:"/",
            component:Index
        },
        {
            path:"/login",
            component:Login

        },
        {
            path:"/con/:nid",
            component:Con
        },
        {
            path:"/phone",
            component:Phone
        },
        {
            path:"/logs",
            component:Logs,
            children:[
                {
                    path:"",
                    component:ShowLogs,
                    redirect:"/messageList/send",
                    children:[
                        {
                            path:"/messageList/:type",
                            component:SendMessage,
                        }
                    ]
                },
                {
                    path:"/write",
                    component:WriteLog,
                },
                {
                    path:"/selectPart",
                    component:selectPart,
                },
                {
                    path:"/partList/:pid",
                    component:selectPart
                }
            ]
        }
    ]
});

router.beforeEach(function (to,from,next) {
    if(to.path=="/login"){
        next();
    }else {
        if (router.app.$store&&router.app.$store.state.login) {
            next();
        } else {
            next("/login");
        }
    }
})

export default router;


