<template>

    <div style="padding-top: 50px;" class="box">

         <div class="form-list">
             <label>标题:</label>
             <input type="text" v-model="title" @blur="save('title')">
         </div>

        <div class="form-list">
            <label>内容:</label>
            <textarea v-model="con" @blur="save('con')"></textarea>
        </div>

        <input type="hidden" v-model="uid">
        <input type="hidden" v-model="uname">
        <router-link  to="/selectPart" class="btn">
              {{uname}}
        </router-link>

        <div class="btn blue" @click="submit()">
            提交
        </div>



    </div>
</template>

<script>
    export default {
        name: 'hello',
        data () {
            return {
                uname:"",
                uid:"",
                title:"",
                con:""
            }
        },
        created(){
            this.uname=this.$store.state.userName?this.$store.state.userName:"请选择接收者";
            this.uid=this.$store.state.userId?this.$store.state.userId:"";
            this.title=this.$store.state.title?this.$store.state.title:"";
            this.con=this.$store.state.con?this.$store.state.con:"";


        },
        methods:{
            submit(){
                console.log(this.uname,this.uid,this.con,this.title,this.$store.state.uid);

                var params="uid1="+this.$store.state.uid+"&uid2="+this.uid+"&title="+this.title+"&con="+this.con;
                fetch("/api/saveLog?"+params).then(function (e) {
                   return e.text();
                }).then((e)=>{
                    if(e=="ok"){
                        alert("ok");
                    }
                })
            },
            save(type){

                if(type=="title") {
                    this.$store.commit("setTitle", this.title)
                }else{
                    this.$store.commit("setCon", this.con)
                }

            }
        }
    }
</script>

<style scoped>
    .box{
        padding: 0 10px;
    }
.form-list{
    width:100%;
    display: flex;
    padding:3px 0px;


}
   .form-list label{
       width: 20%;
      line-height: 40px;
    }

textarea{
    width:100%;height:100px;
}


 .btn{
        display: block;
        width:80%;padding:10px 0px;
        border:1px solid #ccc;
        border-radius: 5px;
     margin:auto;
     color:#000;
    }
    .blue{
        background: #6bb1ff;
        margin-top: 10px;
    }
</style>


